import { Component, OnInit } from '@angular/core';
import { DropshipService } from '../../../../../auth/_services/dropship.service';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';

@Component({
    selector: 'app-dropship-view',
    templateUrl: "dropship-view.component.html",
    styles: []
})
export class DropshipViewComponent implements OnInit {

    dropshipData: any;
    public ImgUrl: any;
    dropship_id: any;

    constructor(private imgBaseUrl: ScriptLoaderService, private toastr: ToastrService, private _dropshipService: DropshipService, private router: Router, private route: ActivatedRoute) {

    }

    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.dropship_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this.ImgUrl = this.imgBaseUrl._imgBaseUrl;

        this.route.params.subscribe(
            params => {
                this.dropship_id = params.id;
                this._dropshipService.getById(params.id).subscribe(
                    data => {
                        // console.log(data);
                        this.dropshipData = data.success;
                    }
                );
            }
        );
    }

    public productDelete(del_id) {
        console.log(del_id);

        let value = confirm('Are you want to delete this product ?');
        if (value) {

            this._dropshipService.del_dropship_product({ product_id: del_id, dropship_id: this.dropship_id }).subscribe(
                data => {
                    console.log(data);
                    this.dropshipData = data.data;
                }
            );
        }
    }

}
